


















export default {
	name: 'fans-control',
	props: {
		styleType: {
			type: String,
			default: 'button'
		},
		followStatus: {
			type: Boolean,
			default: true
		},
		values: {
			type: Array,
			default () {
				return [];
			}
		},
	},
	data() {
		return {
			baseusrl: 'https://www.icoo.tech',
			nopicture: '../../../static/img/nopicture.png'
		}
	},
	methods: {
		followChange(item) {
			let type = '';
			if (item.followState == 0) {
				item.followState = 1;
				type = 0;
			} else {
				item.followState = 0;
				type = 1;
			}
			this.setFollowChange(item, type);
		},
		setFollowChange(item, type) {
			let that = this;
			let data = {
				userCode: this.$store.state.userCode,
				token: this.$store.state.token,
				followUserCode: item.userCode,
				type: type,
			}
			console.log(data)
			this.$api.followOrCancelUser(data, function(res) {
				if (res.data.status == 1) {
					if (item.followState == 1) {
						uni.showToast({
							title: '关注成功',
							icon: 'success'
						});
					} else {
						uni.showToast({
							title: '取消成功',
							icon: 'success'
						});
					}
				} else {
					uni.showToast({
						title: '设置失败',
						icon: 'none'
					});
				}
				console.log(res)
			}, function(err) {
				console.log(err)
			});
		}
	},
}



